import { Component, OnInit, ViewChild } from '@angular/core';
import { Router,NavigationExtras } from '@angular/router';
import { NgForm, NgModel, NgControl, FormsModule } from '@angular/forms';
import { LoopBackAuth, App_loginApi, BooksApi, CityApi,MunicipalityApi } from '../../../../shared/loopback-sdk';
import { ActivatedRoute } from "@angular/router";
import * as moment from 'moment'; //for date manupalation
import {ConfirmationService} from 'primeng/primeng';
import { Lightbox } from 'angular2-lightbox';
import {PaginatorModule} from 'primeng/primeng';
import {Message,MenuItem,SelectItem} from 'primeng/primeng';
import { CookieService } from 'angular2-cookie/core';
declare var window:any;
declare var $;
declare var sdkPrefix;
@Component({
	selector: "viewBook",
	templateUrl: 'view-municipality.component.html',
	providers: [ConfirmationService],
})

export class ViewMunicipalityComponent {
	result: any;
	success: boolean;
	display: boolean;
	public id: number;
	public role: string;
	submited:any=false;

	municipalityData: any = [];
	backLink:any = "";
	busy:any;
	emptyMessage:String="No Result Found";
	displayConfirm: boolean = false;
	msgs: Message[] = [];
	maxDate:Date;
	loading:boolean = true;
	chapterName:any;
	chapterStatus:any="";
	createdAt:any;
	chapterNameSearch:any;
	chapterStatusSearch:any="";
	createdAtSearch:any;
	paginationVal:any=false;
	perPageLimit:number = 10;
	offset:number=0;
	total:number;
	_albums:any = [];
	chapterData:any=[];
	ifEmpty:boolean=false;
	projectCount = 0;
	constructor(private router:Router, private bookApi:BooksApi, private _cookieService: CookieService, private _lightbox: Lightbox, private confirmationService: ConfirmationService, private appLoginApi:App_loginApi,private loopAuth:LoopBackAuth,private route: ActivatedRoute,private cityApi : CityApi,public municipalityApi:MunicipalityApi) {
		this.id = this.route.snapshot.params['id'];
		this.maxDate = new Date();
	}

	ngOnInit(): void {
		this.loading = true;
		let dataObj={};
		let uid=this.id;
		let dataArrObj={
			"id":this.id
		}
		let loginFlagToken = this._cookieService.get(sdkPrefix+'id');

		
		this.municipalityApi.getMunicipality(dataArrObj).subscribe((data)=>{
			
				this.loading = false;
				if(data.result.length>0){
					if(data.result[0].status == 1){
						data.result[0].status = 'Active';
					}else{
						data.result[0].status = 'Inactive';
					}
					this.municipalityData = data.result[0];
					this.projectCount = data.result[0].usedCount;
				}
			},
			err=>{
				this.loading = false;
			}
		);
		
	}

	

	

	activeInactiveClick(){
		this.confirmationService.confirm({
			message: 'Are you sure that you want to proceed?',
			header: 'Confirmation',
			icon: 'fa fa-question-circle',
			accept: () => {
			  // this.msgs = [{severity:'info', summary:'Confirmed', detail:'You have accepted'}];
			  this.activeInactiveSubmit();
			},
			reject: () => {
			 // this.msgs = [{severity:'info', summary:'Rejected', detail:'You have rejected'}];
			}
		});
	}

	activeInactiveSubmit(){
		this.loading = true;
		let dataObj={};
		if(this.municipalityData['status']=='Active'){
			dataObj['status']=0;
		}else{
			dataObj['status']=1;
		}
		var provinceObj={
			city_id:this.id,
			city_name:this.municipalityData['city_name'],
			status:dataObj['status']
		}
		this.msgs.length=0;
		this.cityApi.getMunicipalityCount({"city_id":this.id}).subscribe((data)=>{
			console.log("total :",data.result[0].total);
			let totalMunicipality = data.result[0].total;
			if(totalMunicipality > 0 && provinceObj.status){
				

				this.cityApi.upsert(provinceObj).subscribe((data)=>{
					this.loading = false;
					this.msgs.push({severity:'success', summary:'Success Message', detail:'Book Status Has Been Changed Successfully.'});
						setTimeout(()=>{
							window.location.reload();
						},3000);

				},(err)=>{
					this.msgs.push({severity:'warn', summary:'Error Message', detail:'Somthing Went Wrong. Please try again later.'});
				});

			}else if(totalMunicipality == 0){

				this.loading = false;
				this.msgs.push({severity:'warn', summary:'Error Message', detail:'Please assign municipality first to activate.'});
			}else if(!provinceObj.status && this.projectCount > 0){
				this.loading = false;
				this.msgs.push({severity:'warn', summary:'Error Message', detail:'Projects are already assigned to this province , cannot inactive this province'});
			}
			
		},(err)=>{
			this.loading = false;
		});
		// this.bookApi.addEditBooks(dataObj,bookId).subscribe((respData:any)=>{
		// 	this.loading = false;
		// 	this.msgs.push({severity:'success', summary:'Success Message', detail:'Book Status Has Been Changed Successfully.'});
		// 	setTimeout(()=>{
		// 		window.location.reload();
		// 	},3000);
		// },(error)=>{
		// 	//console.log('respData error',error);
		// 	this.msgs.push({severity:'warn', summary:'Error Message', detail:'Somthing Went Wrong. Please try again later.'});
		// });
	}


	open(data,index: number): void {
		// override the default config
		this._lightbox.open(data, index, { wrapAround: true, showImageNumberLabel: true });
	}
}


